#toolbar {
    width: 400px;
    height: 40px;
    background-color: #282632;
    border-radius: 3px;
    display: flex;
    align-items: center;
    position: relative;
}

#colors {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 350px;
}

#red, #blue, #black, #white, #green{
    height: 20px;
    width: 20px;
    border-radius: 5px;
}

#alignment img {
    height: 20px;
    -drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

#decoration img {
    height: 20px;
}

#decoration img:hover{
    cursor: pointer;
}

#alignment img:hover {
    cursor: pointer;
}

#decoration {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 45px;
}

#alignment {
    width: 120px;
    background-color: #413e4e;
    display: flex;
    justify-content: space-around;
    height: 40px;
    border-radius: 10px;
    align-items: center;
}

#red:hover {
    cursor: pointer;
}

#blue:hover {
    cursor: pointer;
}
#black:hover {
    cursor: pointer;
}
#green:hover {
    cursor: pointer;
}
#white:hover {
    cursor: pointer;
}

#red {
    background-color: red;
}

#blue {
    background-color: blue;
}

#black {
    background-color: black;
}

#white {
    background-color: white;
}

#green {
    background-color: green;
}