@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@font-face {
  font-family: 'Streetline';
  src: local('Streetline'), url(/lazulis-build/static/media/streetline.d69ae683.otf) format('truetype');
}
* {
  margin: 0;
  padding: 0;
}
.app-sidebar {
  margin: 0;
  width: 12vw;
  background-color: #282632;
  height: 100vh;
}

.app-sidebar-note-add {
  color: rgb(105, 105, 105);
  transition: 0.3s;
}

.app-sidebar-note-add:hover {
  background-color: #354C4B;
  border-radius: 3px;
  cursor: pointer;
}

textarea, h1, h2, .app-sidebar-note-add  {
  font-family: 'Streetline';
  color: white;
}

.app-sidebar h2 {
  text-align: center;
  font-weight: 100;
  margin-top: 20px;
}

.app-sidebar-notes {
  margin-top: 50px;
  margin: 10px 5px 5px 5px;
}

.app-sidebar-note {
  display: flex;
  align-items: center;
  transition: 0.3s;
  margin-top: 7px;
  border-radius: 3px;
}

.app-sidebar-note:hover {
  background-color: #354C4B;
  cursor: pointer;
}


.app-sidebar-note p, .app-sidebar-note-add  {
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  color: #88888F;
  padding: 5px 5px 5px 5px;
}
.app-sidebar-note.active {
  background-color: #262330;
  border-radius: 3px;
  font-weight: 500;
}

.app-sidebar-note.active p{
  color: white;
}

.App {
  display: flex;
}

body, textarea {
  background-color: #1E1C26;
}

textarea {
  resize: none;
  border: none;
  outline: none;
  overflow: hidden;
}

#file-btn input {
  display: none;
}


.app-body {
  float: right;
}

.app-body {
  margin-left: 100px;
  margin-top: 60px;
}

.app-body-title textarea{
  height: 62px;
  width: 80vw;
  font-size: 32px;
  text-decoration: none;
  border-radius: 3px;
}

.app-body-markdown textarea{
  width: 80vw;
  margin-top: 20px;
  height: auto;
  resize: none;
  height: 10vh;
  border: none;
  font-size: 16px;
}

.scroll::-webkit-scrollbar {
  display: none;
}

*:focus {
  outline: none;
}

::selection {
  background: #354C4B;;
}

.contextContainer {
  z-index: 1;
  width: 100%;
  height: 500px;
  background: #673ab7;
}
.rightClick {
  z-index: 12;
  position: fixed;
  background: rgb(240, 200, 1);
}
.menuElement {
  color: #222222;
  cursor: pointer;
  padding: 17px 36px;
}
.menuElement:hover {
  color: #fff;
  background: #009688;
}
@font-face {
    font-family: 'Streetline';
    src: local('Streetline'), url(/lazulis-build/static/media/streetline.d69ae683.otf) format('truetype');
}
#bloc {
    font-family: 'Streetline';
    font-weight: 200;
    border: none;
    width: 70vw;
    font-size: 22px;
    height: 2vh;
    margin: 0;
    padding: 7px 10px 7px 10px;
    border-radius: 3px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: 0.1s;
    overflow-y: hidden; /* désactiver la barre de défilement verticale */
    white-space: pre-wrap; /* forcer les sauts de ligne automatiques */
    word-wrap: break-word; /* gérer le saut des mots en fonction de la largeur de la page */
    resize: none; /* désactiver la redimension du textarea */
}

#bloc:hover {
    background-color: #262330;
}

.scroll::-webkit-scrollbar {
    display: none;
  }

#toolbar {
    width: 400px;
    height: 40px;
    background-color: #282632;
    border-radius: 3px;
    display: flex;
    align-items: center;
    position: relative;
}

#colors {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 350px;
}

#red, #blue, #black, #white, #green{
    height: 20px;
    width: 20px;
    border-radius: 5px;
}

#alignment img {
    height: 20px;
    -drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

#decoration img {
    height: 20px;
}

#decoration img:hover{
    cursor: pointer;
}

#alignment img:hover {
    cursor: pointer;
}

#decoration {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 45px;
}

#alignment {
    width: 120px;
    background-color: #413e4e;
    display: flex;
    justify-content: space-around;
    height: 40px;
    border-radius: 10px;
    align-items: center;
}

#red:hover {
    cursor: pointer;
}

#blue:hover {
    cursor: pointer;
}
#black:hover {
    cursor: pointer;
}
#green:hover {
    cursor: pointer;
}
#white:hover {
    cursor: pointer;
}

#red {
    background-color: red;
}

#blue {
    background-color: blue;
}

#black {
    background-color: black;
}

#white {
    background-color: white;
}

#green {
    background-color: green;
}
